body {
  background: #eff1f5;
  padding-bottom: 8rem;
}

button:focus-visible {
  outline: 1px solid #282c3e;
}

header {
  padding: 2rem;
}

header h1 {
  text-align: center;
  font-size: 4rem;
  font-weight: 100;
}

main, footer {
  margin-bottom: 6rem;
  padding: 0 2rem 1rem;
}

main .main-card {
  max-width: 640px;
  background: #fff;
  border-radius: 1rem;
  margin: auto;
  padding: 3rem 2rem;
}

main .main-card label {
  margin-bottom: .5rem;
  display: block;
}

main .main-card form textarea {
  width: 100%;
  resize: none;
  background: #eff1f5;
  border: none;
  padding: 1rem;
  font-size: large;
  display: block;
}

main .main-card form label, main .main-card form h3 {
  font-size: medium;
  font-weight: normal;
}

main .main-card form label:not(:first-child), main .main-card form h3:not(:first-child) {
  margin-top: 1rem;
}

main .main-card form input#image-url {
  width: 100%;
  resize: none;
  background: #eff1f5;
  border: none;
  padding: .75rem 1rem;
  font-size: medium;
  display: block;
}

main .main-card form .image-preview {
  text-align: center;
  margin: auto;
}

main .main-card form .image-preview h3 {
  margin: 1rem 0 .5rem;
}

main .main-card form .image-preview img {
  width: 100%;
  max-width: 16rem;
}

main .main-card form .controls {
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
  display: flex;
}

main .main-card form .controls .color-selection {
  align-items: center;
  gap: .25rem;
  display: flex;
}

main .main-card form .controls .color-selection button {
  width: 2rem;
  height: 2rem;
}

main .main-card form .controls .color-selection button.color-select-red {
  background: #f99;
}

main .main-card form .controls .color-selection button.color-select-green {
  background: #99ffb1;
}

main .main-card form .controls .color-selection button.color-select-blue {
  background: #99e6ff;
}

main .main-card form .controls .color-selection button.color-select-neutral {
  background: #ccc;
}

main .main-card form .controls .color-selection button.selected {
  border: 2px solid #282c3e;
}

main .main-card form .controls > button {
  color: #f2f2f2;
  background: #4d4d4d;
  padding: .5rem 1rem;
}

main .main-card ul {
  border-top: 1px solid #d3d7e3;
  border-bottom: 1px solid #d3d7e3;
  flex-direction: column;
  gap: .25rem;
  margin: 2rem 0;
  padding: 3rem 0;
  display: flex;
}

main .main-card ul li {
  outline: 2px solid #fff;
  align-items: center;
  padding: .5rem;
  display: flex;
}

main .main-card ul li.color-red {
  background: #ffe6e6;
}

main .main-card ul li.color-green {
  background: #e6ffeb;
}

main .main-card ul li.color-blue {
  background: #e6f9ff;
}

main .main-card ul li.color-neutral {
  background: #f2f2f2;
}

main .main-card ul li.placeholder-todo {
  text-align: center;
  padding: .5rem;
}

main .main-card ul li.placeholder-todo .content {
  margin: auto;
}

main .main-card ul li:focus {
  outline: .5px solid #282c3e;
}

main .main-card ul li:focus-visible {
  outline: 1.5px solid #282c3e;
}

main .main-card ul li button.toggle {
  padding: .5rem;
  font-size: larger;
}

main .main-card ul li button.toggle:active {
  transform: translateY(1px);
}

main .main-card ul li div.content {
  flex-direction: column;
  flex: auto;
  gap: 1rem;
  padding: .75rem .5rem;
  display: flex;
}

main .main-card ul li div.content img {
  max-height: 12rem;
  object-fit: contain;
  margin-right: auto;
}

main .main-card ul li div.content.done {
  color: gray;
  text-decoration: line-through;
}

main .main-card ul li div.content.done img {
  filter: brightness(50%);
}

main .main-card ul li button.delete {
  width: 1rem;
  height: 1rem;
  color: #262626;
  background: #d9d9d9;
  justify-content: center;
  align-items: center;
  padding: .5rem;
  display: flex;
}

main .main-card ul li button.delete:active {
  transform: translateY(1px);
}

main .main-card ul li .handle {
  width: 1rem;
  height: 1rem;
  cursor: grab;
  background: #d9d9d9;
  justify-content: center;
  align-items: center;
  margin-left: 1rem;
  padding: 1rem;
  font-size: larger;
  display: flex;
}

main .main-card input.search {
  width: 100%;
  resize: none;
  background: #eff1f5;
  border: none;
  padding: .75rem 1rem;
  font-size: medium;
  display: block;
}

main .main-card .list-footer {
  place-items: center;
  gap: 1rem;
  padding: 2rem 0 0;
  display: grid;
}

@media screen and (min-width: 640px) {
  main .main-card .list-footer {
    grid-template-columns: 1fr auto 1fr;
    gap: 0;
  }
}

main .main-card .list-footer .filter {
  order: -1;
  gap: .25rem;
  display: flex;
}

@media screen and (min-width: 640px) {
  main .main-card .list-footer .filter {
    order: unset;
  }
}

main .main-card .list-footer .filter button {
  color: #f2f2f2;
  background: #4d4d4d;
  padding: .5rem 1rem;
}

main .main-card .list-footer .filter button.selected {
  color: #262626;
  background: #d9d9d9;
  box-shadow: inset 0 0 5px #bfbfbf;
}

main .main-card .list-footer .clear-completed {
